import { useDispatch, useSelector } from "react-redux";
import { activateNotify, setNotify } from "../reducers";
import { adminAuthPayload } from "../payload";
import { useEffect } from "react";
import { useNavigate } from "react-router";

export default function LoginAuthPage() {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const token = useSelector(state => state.authentication.isTokenAvailable);

    useEffect(() => {
      if(token){
          navigate("/self/base/create");
      }
    }, [token]);

    const notifyResponse = (type,message) => {
        dispatch(activateNotify());
        dispatch(setNotify({
          notifyType: type === 200 ? 'success' : 'error',
          notifyMessage: message
        }))
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        const elements = event.target;
        adminAuthPayload(elements,"login",notifyResponse,dispatch);
    }

    return (
      !token
      ?
        <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
          <div className="sm:mx-auto sm:w-full sm:max-w-sm">
            <h2 className="mt-10 text-center text-2xl font-bold leading-9 tracking-tight text-gray-900">
              Log in to your account
            </h2>
          </div>
  
          <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
            <form className="space-y-6" onSubmit={handleSubmit} >
              <div>
                <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                  Email address
                </label>
                <div className="mt-2">
                  <input
                    id="email"
                    name="email"
                    type="email"
                    autoComplete="email"
                    required
                    className="block w-full rounded-md border-0 p-[10px] text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  />
                </div>
              </div>
  
              <div>
                <div>
                  <label htmlFor="password" className="block text-sm font-medium leading-6 text-gray-900">
                    Password
                  </label>
                </div>
                <div className="mt-2">
                  <input
                    id="password"
                    name="password"
                    type="password"
                    autoComplete="current-password"
                    required
                    className="block w-full rounded-md border-0 p-[10px] text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  />
                </div>
              </div>
  
              <div>
                <button
                  type="submit"
                  className="flex w-full justify-center rounded-md bg-indigo-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                >
                  Sign in
                </button>
              </div>
            </form>
          </div>
        </div>
      : <></>
    )
}
  